<template>
  <div id="record4" class="record">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <div class="field-item">
      <label for="time" class="fonts30">选择日期</label>
      <button class="order-time" @click="openPicker">请选择日期</button>
      <mt-datetime-picker
        id="time"
        ref="picker"
        v-model="pickerValue"
        type="date"
        :start-date="startTime"
        @confirm="handleConfirm">
      </mt-datetime-picker>
    </div>
    <div class="date-box">
      <label class="time-txt" v-for="(item,index) in orderTime"><b @click="deleteTime(index)">&times;</b>{{dateFormat(item)}}</label>
    </div>
    <div class="strong-line2"></div>

    <div class="field-item chose-field">
      <label>咨询方式<b>（可多选）</b></label>
      <div class="btn-box three">
        <button class="choseBtn" :class="acceptWay[0]==true?'active':''" @click="acceptBtn(0,'way')">语音</button>
        <button class="choseBtn" :class="acceptWay[1]==true?'active':''" @click="acceptBtn(1,'way')">视频</button>
        <button class="choseBtn" :class="acceptWay[2]==true?'active':''" @click="acceptBtn(2,'way')">面对面</button>
      </div>
    </div>
    <div class="strong-line2"></div>
    <div class="field-item chose-field">
      <label>咨询时段<b>（可多选）</b></label>
      <div class="time-list">
        <div class="time-item" v-for="(item,index) in timeList" @click="setTime(index)" :class="activeClass[index]==true?'active':''">
          <span>{{item.start}}</span>
          <p>￣</p>
          <span>{{item.end}}</span>
        </div>
      </div>
    </div>
    <div class="tips-txt">
      温馨提示： 如果添加的可咨询时段涉及到已设置的时段，对未安排咨询的时段，则新的设置会覆盖原来的时段设置，对已安排咨询的时段，则不受影响。
    </div>
    <div class="btnBox">
      <button class="closeBtn" @click="goTo('/eapTime')">保存</button>
    </div>

  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "record3",
    data(){
      return{
        showTitle:'添加咨询时段',
        selectData: {
          defaultIndex: 0, //默认选中的是第几个
          selectStatus: false, // 通过selectStatus来控制下拉框的显示/隐藏
        },
        pickerValue:"",
        orderTime:[],
        startTime:new Date(),
        acceptWay:[true,false,false],
        timeList:[
          {start:"9:00",end:"9:50"},{start:"10:00",end:"10:50"},{start:"11:00",end:"11:50"},
          {start:"12:00",end:"12:50"},{start:"13:00",end:"13:50"},{start:"14:00",end:"14:50"},
          {start:"15:00",end:"15:50"},{start:"16:00",end:"16:50"},{start:"17:00",end:"17:50"},
          {start:"18:00",end:"18:50"},{start:"19:00",end:"19:50"},{start:"20:00",end:"20:50"},
          {start:"21:00",end:"21:50"},
        ],
        selectTime:[],
        activeClass: []
      }
    },
    created(){
      let len=this.timeList.length;
      let i=0;
      while (i<len){
        this.activeClass.push(false)
        i++
      }
    },
    watch:{
    },
    computed:{
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$refs.child.backUp();
      },
      openPicker() {
        this.$refs.picker.open();
      },
      handleConfirm(data){
        this.orderTime.push(data);
      },
      //时间格式化函数，此处仅针对yyyy-MM-dd hh:mm:ss 的格式进行格式化
      dateFormat:function(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 在日期格式中，月份是从0开始的，因此要加0
         * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 拼接
        return year+"-"+month+"-"+day;
      },
      acceptBtn(id, flag) {
      console.log(id,flag)
      if (flag == 'type') {
        this.$set(this.acceptType,id,!this.acceptType[id]);
      } else if (flag == 'way') {
        this.$set(this.acceptWay,id,!this.acceptWay[id]);
      }
    },
      deleteTime(index){
        this.orderTime.splice(index,1);
      },
      setTime(index){
        this.selectTime.push(this.timeList[index]);
        this.$set(this.activeClass,index,!this.activeClass[index]);
      }
    }
  }
</script>

<style scoped lang="less">
  .field-item.chose-field{
    height: auto;
    padding: 14px 0;
    label{
      display: block;
      float: none;
      width: auto;
      font-size: 30px;
      padding: 10px 36px;
      border-bottom: 1px solid #ededed;
      b{
        font-weight: normal;
        color: #b1b1b1;
      }
    }
  }

  .btn-box{
    padding-top: 14px;
    padding-bottom: 10px;
    .choseBtn{
      width:120px;
      height:40px;
      background:#fff;
      border: 2px solid #bfbfbf;
      color: #B1B1B1;
      border-radius:6px;
      text-align: center;
      line-height: 40px;
      margin-right: 260px;
      &.active{
        color: #fff;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border: 0;
      }
      &:last-child{
        margin-right: 0;
      }
    }
    &.three{
      .choseBtn{
        margin-right: 66px;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
  .order-time{
    width: 440px;
    height: 60px;
    background: url("../../assets/img/consult/downArrow.png") no-repeat right center;
    background-size: 27px 15px;
    text-align: left;
    padding-left: 50px;
    color: #b1b1b1;
    font-size: 26px;
  }
  .date-box{
    min-height: 100px;
    padding: 8px 36px 28px;
    width: 750px;
    margin: auto;
    overflow: auto;
    .time-txt{
      float: left;
      font-size: 30px;
      line-height: 34px;
      width: 33.33%;
      padding-top: 16px;
      b{
        float: left;
        width:34px;
        height:34px;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border-radius: 50%;
        color: #fff;
        line-height: 34px;
        text-align: center;
        margin-right: 5px;
      }
    }
  }
  .time-list{
    padding: 28px 36px 0;
    overflow: auto;
    .time-item{
      width:90px;
      height:90px;
      background:rgba(255,255,255,1);
      border:2px solid rgba(191,191,191,1);
      border-radius:10px;
      color:rgba(177,177,177,1);
      line-height: 20px;
      text-align: center;
      padding-top: 3px;
      margin-right: 56px;
      margin-bottom: 28px;
      float: left;
      &:nth-child(5n){
        margin-right: 0;
      }
      &.active{
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        color: #fff;
      }
      span{
        font-size:24px;
      }
      p{
        font-size: 24px;
        padding-top: 16px;
      }
    }
  }
  .btnBox{
    margin-bottom: 28px;
  }
</style>





