<template>
    <div id="enter">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="strong-line"></div>
      <div class="e-content">
        <h3 class="e-title"><img src="../../assets/img/enter/tianxie.png" alt="">填写入驻信息</h3>
        <p class="e-text">
          1. 咨询师入驻选择“咨询师入驻”，咨询机构入驻
          选择“咨询机构入驻”；
        </p>
        <p class="e-text">
          2. 按要求填写入驻信息并提交，完整填写有助于您
          顺利通过审核成功入驻本平台；
        </p>
      </div>
      <div class="e-content">
        <h3 class="e-title"><img src="../../assets/img/enter/weibiaoti.png" alt="">等待审核</h3>
        <p class="e-text">
          1. 我们会有工作人员在3~5个工作日内完成对您提
          交的入驻信息的审核；
        </p>
        <p class="e-text">
          2. 资料合格后，通知您进行面试。
        </p>
        <p class="e-text">3. 通过面试</p>
      </div>
      <div class="e-content">
        <h3 class="e-title"><img src="../../assets/img/enter/chenggong.png" alt="">入驻成功</h3>
      </div>
      <button class="btn" @click="goTo('/enterSelect')">确定</button>
    </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader"
    export default {
      name: "index",
      data() {
        return {
          showTitle:"入驻申请"
        }
      },
      components: {
        appHeader
      },
      methods:{
        backUp(){
          this.$refs.child.backUp();
        }
      }
    }
</script>

<style scoped lang="less">
  .e-content{
    border-bottom: 1px solid #ededed;
    padding: 0 36px 10px;
    .e-title{
      font-size: 36px;
      font-weight: 400;
      padding: 28px 0;
      img{
        width: 36px;
        height: 36px;
        vertical-align: text-bottom;
        margin-right: 28px;
      }
    }
    .e-text{
      font-size:30px;
      font-weight:400;
      color:rgba(84,84,84,1);
      line-height:45px;
      margin-bottom: 10px;
    }
  }
  .btn{
    position: fixed;
    bottom: 60px;
    right: 0;
    left: 0;
    margin: auto;
  }
</style>



