import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/enter/index'
import enterSelect from '@/components/enter/enterSelect'
import appfilter from '@/components/public/appfilter'
import appSelect from "@/components/public/appSelect"
import notification from '@/components/public/notification'
import area from "../components/public/area"
import upload from "@/components/public/upload"
import step1 from "@/components/eap/step1"
import step2 from "@/components/eap/step2"
import step3 from "@/components/eap/step3"
import step4 from "@/components/eap/step4"
import organization from "@/components/organization/index"
import organizationNext from "@/components/organization/next"
import preview from "@/components/public/preview"
import order from "@/components/eap/orderList"
import orderDetail from "@/components/eap/orderDetail"
import record from "@/components/eap/record"
import record2 from "@/components/eap/record2"
import record3 from "@/components/eap/record3"
import record4 from "@/components/eap/record4"
import record5 from "@/components/eap/record5"
import eapTime from "@/components/eap/eapTime"
import addTime from "@/components/eap/addTime"


Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes:[
    {
      path:'',
      component:index
    },
    {
      path:'/enterSelect',
      component:enterSelect
    },

    {
      path:'/appfilter',
      component: appfilter
    },
    {
      path:'/appSelect',
      component: appSelect
    },
    {
      path:'/notification',
      component:notification
    },
    {
      path:'/area',
      component:area
    },
    {
      path:'/upload',
      component:upload
    },
    {
      path:'/eap/step1',
      component:step1,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path:'/eap/step2',
      component:step2,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path:'/eap/step3',
      component:step3,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path:'/eap/step4',
      component:step4,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path:'/organization',
      component:organization,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path:'/organization/next',
      component:organizationNext,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path:'/preview/:flag',
      component:preview
    },
    {
      path:'/order',
      component:order,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path:'/order/detail/:flag',
      component:orderDetail
    },
    {
      path:'/record',
      component:record
    },
    {
      path:'/record2',
      component:record2
    },
    {
      path:'/record3',
      component:record3
    },
    {
      path:'/record4',
      component:record4
    },
    {
      path:'/record5',
      component:record5
    },
    {
      path:'/eapTime',
      component:eapTime
    },
    {
      path:'/addTime',
      component:addTime
    },

  ]
})



