<template>
  <div id="step3" class="step-container">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <ul class="step-list">
      <li :class="item.index==activeClass[index]?'active':''" v-for="(item,index) in stepList">
        <img :src="getUrl(item.index)" alt="" v-if="activeClass[index]==item.index">
        <img :src="getUrl('2'+item.index)" alt="" v-if="activeClass[index]!=item.index">
        <span>{{item.txt}}</span>
      </li>
    </ul>

    <div class="field-item chose-field">
      <label>咨询师类别</label>
      <div class="btn-box">
        <button class="choseBtn" :class="eapType==0?'active':''" @click="eapType=0">个人</button>
        <button class="choseBtn" :class="eapType==1?'active':''" @click="eapType=1">机构</button>
      </div>
    </div>
    <div class="field-item chose-field">
      <label>可接咨询类型<b>（可多选）</b></label>
      <div class="btn-box">
        <button class="choseBtn" :class="acceptType[0]==true?'active':''" @click="acceptBtn(0,'type')">个人</button>
        <button class="choseBtn" :class="acceptType[1]==true?'active':''" @click="acceptBtn(1,'type')">家庭</button>
      </div>
    </div>
    <div class="field-item chose-field">
      <label>可接咨询方式<b>（可多选）</b></label>
      <div class="btn-box three">
        <button class="choseBtn" :class="acceptWay[0]==true?'active':''" @click="acceptBtn(0,'way')">语音</button>
        <button class="choseBtn" :class="acceptWay[1]==true?'active':''" @click="acceptBtn(1,'way')">视频</button>
        <button class="choseBtn" :class="acceptWay[2]==true?'active':''" @click="acceptBtn(2,'way')">面对面</button>
      </div>
    </div>
    <mt-field label="咨询电话" placeholder="请输入咨询电话" v-model="phone" type="number"></mt-field>
    <div class="field-item">
      <label for="tool">视频工具</label>
      <select name="" id="tool">
        <option>请选择</option>
        <option>微信</option>
        <option>QQ</option>
      </select>
    </div>
    <mt-field label="号码" placeholder="请输入号码" v-model="num" type="number"></mt-field>
    <div class="e-field">
      <h3 class="area-title">咨询地址</h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder=" 参考格式：省-市-区/县-具体门牌号"></textarea>
      </div>
    </div>
    <div class="strong-line" style="padding-bottom: 20px"></div>

    <h3 class="tip-box">咨询费用结算账户</h3>
    <div class="line"></div>
    <mt-field label="开户行名称" placeholder="请输入开户行名称" v-model="num"></mt-field>
    <mt-field label="银行卡号" placeholder="请输入银行卡号" v-model="num"></mt-field>
    <div class="tips-txt">温馨提示：1. 为保证资金安全，只能绑定咨询师本人的银行卡。2. 该银行卡用于咨询费的结算支付，为保证资金顺利到账，请务必填写</div>

    <div class="btn-box">
      <router-link to="/preview/eap"><div class="submit-btn">预览</div></router-link>
      <router-link to="/eap/step4"><div class="submit-btn" @click="goStep(4)">保存并提交</div></router-link>
    </div>
  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "step2",
    data(){
      return{
        imgUrl:"",
        activeClass:[],
        imgBaseUrl:"../../assets/img/seminar/",
        stepList:this.$store.state.stepList,
        showTitle:'咨询师入驻',
        eapType:0,
        acceptType:[true,false],
        acceptWay:[true,false,false],
        phone:'',
        num:""
      }
    },
    created(){
    },
    mounted(){
      this.activeClass=this.$store.state.stepClass;
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$store.commit("stepGo",2);
        this.$refs.child.backUp();
      },
      //接收子组件emit的事件
      getImgUrl(data) {
        //data  得到的就是返回的图片路径的相关参数
        console.log(data)
      },
      //获取图片地址
      getUrl(icon){
        return require("@/assets/img/seminar/"+icon+".png");
      },
      acceptBtn(id, flag) {
        console.log(id,flag)
        if (flag == 'type') {
          this.$set(this.acceptType,id,!this.acceptType[id]);
        } else if (flag == 'way') {
          this.$set(this.acceptWay,id,!this.acceptWay[id]);
          console.log(this.acceptWay)
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .field-item.chose-field{
    height: auto;
    padding-bottom: 14px;
    label{
      display: block;
      float: none;
      width: auto;
      b{
        font-weight: normal;
        color: #b1b1b1;
      }
    }
  }
  .btn-box{
    .choseBtn{
      width:120px;
      height:40px;
      background:#fff;
      border: 2px solid #bfbfbf;
      color: #B1B1B1;
      border-radius:6px;
      text-align: center;
      line-height: 40px;
      margin-right: 260px;
      &.active{
        color: #fff;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border: 0;
      }
      &:last-child{
        margin-right: 0;
      }
    }
    &.three{
      .choseBtn{
        margin-right: 66px;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
</style>



