<template>
  <div id="step2" class="step-container">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <ul class="step-list">
      <li :class="item.index==activeClass[index]?'active':''" v-for="(item,index) in stepList">
        <img :src="getUrl(item.index)" alt="" v-if="activeClass[index]==item.index">
        <img :src="getUrl('2'+item.index)" alt="" v-if="activeClass[index]!=item.index">
        <span>{{item.txt}}</span>
      </li>
    </ul>
    <div v-for="(item,index) in addQual">
      <div class="field-item">
        <label for="qualification">资质名称</label>
        <select name="" id="qualification"  v-model="qualification[index]">
          <option>请选择</option>
          <option>二级心理咨询师</option>
          <option>三级心理咨询师</option>
          <option>中国心理学会注册心理师</option>
          <option>中国心理学会注册督导师</option>
        </select>
      </div>
      <div class="field-item upState">
        <div class="field-title">资质证书</div>
        <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
                @upload="getImgUrl" class="upload"></upload>
        <button @click="addUpload" class="addBtn">增加一项</button>
      </div>
    </div>
    <div class="field-item upState id-card">
      <div class="field-title">学历证书</div>
      <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
              @upload="getImgUrl" class="upload" v-for="item in addEdu"></upload>
      <!--<button @click="addUpload2" class="addBtn">增加一项</button>-->
    </div>

    <div class="e-field">
      <h3 class="area-title">个人简介</h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="2000字以内"></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title">培训经历</h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="2000字以内，填写格式：培训机构名称、培训课程名称、时间 段、培训经历或详情。"></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title">教育经历</h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="2000字以内，填写格式：学校、学历、专业、时间段、在校经 历。"></textarea>
      </div>
    </div>


    <div class="btn-box">
      <router-link to="/eap/step3"><div class="submit-btn" @click="goStep(3)">保存并下一步</div></router-link>
    </div>
  </div>
</template>

<script>
  import eapArea from "@/components/public/area";
  import upload from "@/components/public/upload";
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "step2",
    data(){
      return{
        imgUrl:"",
        activeClass:[],
        imgBaseUrl:"../../assets/img/seminar/",
        stepList:this.$store.state.stepList,
        showTitle:'咨询师入驻',
        addEdu:[1],
        addQual:[1],
        qualification :[]
      }
    },
    created(){

    },
    mounted(){
      this.activeClass=this.$store.state.stepClass;
    },
    components:{
      upload,
      appHeader,
      eapArea
    },
    methods:{
      //接收子组件emit的事件
      getImgUrl(data) {
        //data  得到的就是返回的图片路径的相关参数
        console.log(data)
      },
      //获取图片地址
      getUrl(icon){
        return require("@/assets/img/seminar/"+icon+".png");
      },
      backUp(){
        this.$store.commit("stepGo",1);
        this.$refs.child.backUp();
      },
      showArea(){
        this.popupVisible=true;
      },
      hideArea(){
        this.popupVisible=false;
      },
      changeCity(data){
        console.log(data);
        this.btnTxt=data;
      },
      addUpload(){
        this.addQual.push(1);
      },
      addUpload2(){
        this.addEdu.push(1);
      }
    }
  }
</script>

<style lang="less">
  #step2{
    .addBtn{
      position: absolute;
      bottom: 28px;
      right: 240px;
      color: #4facfe;
    }
    .field-item.upState{
      height: auto;
      overflow: auto;
      padding-bottom: 6px;
    }
  }
</style>



