<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!-- 这里是会被缓存的视图组件，比如 page1-->
      </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive">
      <!-- 这里是不被缓存的视图组件，比如,page2 , page3 -->
    </router-view>
  </div>
</template>

<script>

export default {
  name: 'App'
}
</script>

<style lang="less">
* {
  font-family:SimHei;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -box-sizing: border-box;
  padding: 0;
  margin: 0;
  list-style: none;
  box-sizing: border-box;
  font-size: 24px;
}
html,body{
  background-color: #fff;
  color: #545454;
  /*scroll-behavior: smooth;*/
}
a {
  color: #545454;
  text-decoration: none;
  cursor: pointer;
}
.container{
  width: 750px;
  margin: 0 auto;
}
button{
  border: 0;
  outline: none;
  -webkit-appearance: none;
  background-color: transparent;
}
img {
  border: none;
  max-width: 100%;
  height: auto;
}
input{ -webkit-appearance: none; outline: none}

.line{
  border-bottom: 1px solid #ededed; width: 750px;clear: both
}
.strong-line{
  border-bottom: 14px solid #EDEDED;
  padding-bottom: 16px;
}
.strong-line2{
  border-bottom: 14px solid #EDEDED;
}
.shadow img{
  box-shadow:0px 3px 5px 0px rgba(0, 0, 0, 0.35);
  border-radius: 10px;
}
  .btn{
    width:678px;
    height:100px;
    line-height: 100px;
    text-align: center;
    background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
    border-radius:10px;
    font-size:36px;
    font-weight:400;
    color:rgba(255,255,255,1);
  }
.area-title{
  width: 100%;
  padding-left: 36px;
  padding-right: 36px;
  padding-top: 10px;
  line-height: 80px;
  font-size: 30px;
  font-weight: 400;
  b{
    font-weight: 400;
    color: #b1b1b1;
  }
}

.area-title2{
  width: 100%;
  padding:28px 36px 20px;
  font-size: 30px;
  font-weight: 400;
  color:rgba(84,84,84,1);
}
.area-box{
  width:678px;
  height:200px;
  margin: auto;
  &.height100{
    height: 100px;
  }
  &.height300{
    height: 300px;
  }
  .area-input{
    width: 100%;
    height: 100%;
    padding: 12px 20px;
    outline: none;
    border: 1px solid #b1b1b1;
    -webkit-appearance: none;
    -moz-appearance: none;
  }
}

h3.tip-box{
  width: 100%;
  padding-left: 36px;
  padding-right: 36px;
  padding-top: 10px;
  line-height: 100px;
  font-size: 36px;
  font-weight: 400;

}
.field-item{
  height: 84px;
  width: 750px;
  padding: 0 36px;
  font-size: 30px;
  line-height: 60px;
  border-bottom: 1px solid #eee;
  padding-top: 10px;
  &.upState{
    height: 176px;
    position: relative;
    .field-title{
      position: absolute;
      top: 14px;
      left: 36px;
      font-size: 30px;
    }
  }
  label{
    float: left;
    width: 220px;
    font-size: 30px;
  }
  input[type='radio']{
    width: 30px;
    height: 30px;
    overflow: visible;
    background: url("assets/img/consult/radiobBg2.png") no-repeat center;
    background-size: 30px 30px;
    vertical-align: sub;
    border: 0;
    outline: 0;
    -webkit-appearance: none;
  }

  input:checked{
    position: relative;
    background: #fff;
  }

  input:checked::after {
    position: absolute;
    background: url("assets/img/consult/radioicon.png") no-repeat center;
    background-size: 30px 30px;
    content: "";
    width: 30px;
    height: 30px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
}
.btn-box{
  text-align: center;
  .submit-btn{
    width:678px;
    height:100px;
    background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
    border-radius:10px;
    font-size: 36px;
    color: #fff;
    margin: 36px auto;
    line-height: 100px;
  }
}
.show-btn{
  appearance: none; /* 去掉默认图标 */
  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  background: url("assets/img/consult/downArrow.png") no-repeat scroll 95% center transparent ;/* 自己的图*/
  background-size: 18px 10px;
  /* 以下是圆角和边框等其他设置： */
  width:430px;
  height:60px;
  line-height:28px;
  border:0;
  outline: none;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  padding:0 20px 0 10px;
  text-align: left;
  color: #545454;
}
.pop-box{
  animation: slideInUp .5s;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.7);
  z-index: 9;
  left: 0;
  bottom: 0;
  .close-btn{
    background: #fff;
    width: 100%;
    height: 60px;
    position: absolute;
    font-size: 30px;
    left: 0;
    bottom: 353px;
    color: #4facfe;
    text-align: right;
    padding-right: 36px;
    border-radius: 10px 10px 0px 0px;
  }
}
@-webkit-keyframes slideInUp {
  from{
    bottom:-100%;
  }
  to{
    bottom:0;
  }
}

.step-list{
  margin-top: 28px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ededed;
  padding-left: 30px;
  li{
    display: inline-block;
    width: 120px;
    height: 120px;
    margin-right: 70px;
    text-align: center;
    position: relative;
    &:after{
      content: "……";
      position: absolute;
      display: inline-block;
      top: 16px;
      right: -60px;
      color: #b1b1b1;
    }
    &:last-child{
      margin-right: 0;
      &:after{
        display: none;
      }
    }
    &.active{
      span{
        color:#4facfe;
      }
      &:after{
        color: #545454;
      }
    }
    img{
      margin: 0 30px 10px;
      width: 60px;
      height: 60px;
    }
    span{
      color: #b1b1b1;
      font-size:24px;
    }
  }
}

.step-container{
  .upload{
    overflow: hidden;
    display: inline-block;
  }
  .avatar{
    margin-left: 200px;
  }
  .field-item label{
    width: 186px;
  }
  .tips{
    font-size:24px;
    font-weight:400;
    color:rgba(177,177,177,1);
    line-height:36px;
    position: absolute;
    bottom: 28px;
    width: 320px;
    right: 36px;
    text-align: justify;
  }
}

  .tips-txt{
    color: #b1b1b1;
    font-size: 24px;
    line-height: 36px;
    margin: 24px 36px;
    text-align: justify;
  }

.closeBtn{
  display: block;
  width:678px;
  height:100px;
  background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
  border-radius:10px;
  color: #fff;
  text-align: center;
  line-height: 100px;
  font-size: 36px;
  margin: auto;
}
.fonts36{
  font-size: 36px !important;
}
.fonts30{
     font-size: 30px !important;
}
</style>



