        name: "orderList",
        data(){
          return{
            showTitle:"咨询订单",
            active:"tab-container1",
            tabIndex:''
          }
        },
        components:{
          appHeader
        },
        methods:{
          backUp () {
            this.$refs.child.backUp();
          },
          tabToggle:function (e) {
            let el=e.target;
            let child=el.parentNode.children;
            for (let i=0;i<=4;i++){
              child[i].classList.remove('active');
            }
            el.classList.add('active');
          }
        }
    }
</script>

<style scoped lang="less">
  .order-item{
