        vertical-align: middle;
        margin-left: 36px;
        margin-right: 60px;
        width:80px;
        height:80px;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border-radius:10px;
      }
      .eap-name{
        font-size:30px;
        font-weight:400;
        color:rgba(84,84,84,1);
      }
      .state-txt{
        font-size:30px;
        font-weight:400;
        color:#4facfe;
        float: right;
        margin-right: 36px;
      }
    }
    .order-data{
      width: 100%;
      overflow: auto;
      padding: 28px 36px 0 36px;
      border-bottom: 1px solid #ededed;
      .data-left{
        float: left;
        width: 100%;
        p{
          padding-bottom: 18px;
        }
      }
    }
  }

</style>



