<template>
  <div id="record3" class="record">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box">来访者情况</h3>
    <div class="e-field">
      <h3 class="area-title2">来访者主诉及咨询期望</h3>
      <div class="area-box height300">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title2">来访者基本情况</h3>
      <div class="area-box height300">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="btnBox">
      <button class="closeBtn" @click="goTo('/record4')">下一步</button>
    </div>

  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "record3",
    data(){
      return{
        showTitle:'个案记录',
      }
    },
    created(){
    },
    watch:{
    },
    computed:{
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$refs.child.backUp();
      },
    }
  }
</script>

<style scoped lang="less">
  .record{
    padding-bottom: 150px;
    overflow: auto;
  }

  .tip-box{
    border-bottom: 1px solid #ededed;
  }

  .btnBox{
    width: 100%;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    padding-bottom: 28px;
  }

</style>




