<template>
  <div id="step4">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>

    <ul class="step-list">
      <li :class="item.index==activeClass[index]?'active':''" v-for="(item,index) in stepList">
        <img :src="getUrl(item.index)" alt="" v-if="activeClass[index]==item.index">
        <img :src="getUrl('2'+item.index)" alt="" v-if="activeClass[index]!=item.index">
        <span>{{item.txt}}</span>
      </li>
    </ul>
    <p class="succ-txt">您的认证信息已成功提交！工作人员将于3~5个工作日内完成审核，请保持随时关注！</p>
    <div class="tips-txt">
      温馨提示：提交审核并通过后，入驻资料无法自助修改（需联系客服人工修改），请您检查并确保您填写的信息无误后再点击“提交”按钮，谢谢您的理解与配合！我们的技术人员正在努力开发中，以期为您带来更好的服务体验！
    </div>
    <router-link to="/" class="closeBtn">完成</router-link>
  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "step4",
    data(){
      return{
        imgUrl:"",
        activeClass:[],
        imgBaseUrl:"../../assets/img/seminar/",
        stepList:this.$store.state.stepList,
        showTitle:'咨询师入驻',
      }
    },
    created(){
    },
    mounted(){
      this.activeClass=this.$store.state.stepClass;
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$store.commit("stepGo",3);
        this.$refs.child.backUp();
      },
      //接收子组件emit的事件
      getImgUrl(data) {
        //data  得到的就是返回的图片路径的相关参数
        console.log(data)
      },
      //获取图片地址
      getUrl(icon){
        return require("@/assets/img/seminar/"+icon+".png");
      },
    }
  }
</script>

<style scoped lang="less">
  .succ-txt{
    font-size: 30px;
    line-height: 36px;
    color: #545454;
    padding: 28px 36px;
    border-bottom: 1px solid #ededed;
    text-align: justify;
  }
  .tips-txt{
    line-height: 36px;
  }
  .closeBtn{
    position: fixed;
    bottom: 40px;
    right: 36px;
    left: 36px;
    margin: auto;
  }
</style>



