<template>
    <div id="enterSelect">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="strong-line"></div>
      <div class="btn" @click="goTo('/eap/step1')">咨询师入驻</div>
      <div class="btn" @click="goTo('/organization')">咨询机构入驻</div>
    </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader"
  export default {
    name: "enterSelect",
    data() {
      return {
        showTitle:"入驻类型"
      }
    },
    components: {
      appHeader
    },
    methods:{
      backUp(){
        this.$refs.child.backUp();
      }
    }
  }
</script>


<style scoped lang="less">
  .btn{
    margin: 40px auto 0;
  }
</style>



