<template>
    <div id="record" class="record">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="strong-line"></div>
      <h3 class="tip-box">个案记录</h3>
      <mt-field label="咨询时长：" placeholder="包含等待时间" v-model="time" type="number" :class="'timeAfter'"></mt-field>
      <div class="field-item">
        <label>与工作相关：</label>
        <label class="radio-label">
          <input type="radio" name="withWork" value="true" id="male " v-model="withWork">
          <span>是</span>
        </label>
        <label class="radio-label" for="female">
          <input type="radio" name="withWork" value="false" id="female" v-model="withWork">
          <span>否</span>
        </label>
      </div>
      <div class="chk-box">
        <label  v-for="(item,index) in questionList" :key="index" @click="showR=false">
          <input type="radio" :value="item" name="question" v-model="activeValue">
          {{item}}
        </label>
      </div>
      <div :class="activeValue=='一般心理困扰'?'show':'hide'">
        <div class="field-item">
          <label>问题类型：</label>
          <select name="questionType1" v-model="questionType" v-on:change="showR=true">
            <option value="">请选择</option>
            <option value="1">情感家庭</option>
            <option value="2">子女教育</option>
            <option value="3">人际交往</option>
            <option value="4">职场压力</option>
            <option value="5">生涯发展</option>
            <option value="6">性心理</option>
            <option value="7">个人成长</option>
            <option value="8">孕期产后</option>
          </select>
        </div>
        <div class="chk-box chk-box2">
          <div :class="questionType=='1'?'show':'hide'">
            <label v-for="(item,index) in queList[1]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[1].length+1+".其他"}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
          <div :class="questionType=='2'?'show':'hide'">
            <label v-for="(item,index) in queList[2]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='3'?'show':'hide'">
            <label v-for="(item,index) in queList[3]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='4'?'show':'hide'">
            <label v-for="(item,index) in queList[4]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[4].length+1+".其他"}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
          <div :class="questionType=='5'?'show':'hide'">
            <label v-for="(item,index) in queList[5]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='6'?'show':'hide'">
            <label v-for="(item,index) in queList[6]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
          </div>
          <div :class="questionType=='7'?'show':'hide'">
            <label v-for="(item,index) in queList[7]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[7].length+1+".其他"}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
          <div :class="questionType=='8'?'show':'hide'">
            <label v-for="(item,index) in queList[8]" :key="index">
              <input type="radio" :value="item" name="que1" v-model="queValue">
              {{item}}
            </label>
            <label>
              <input type="radio" :value="other" name="que1">
              {{queList[8].length+1+".其他"}}
              <input type="text" class="otherInput" v-model="other">
            </label>
          </div>
        </div>
      </div>
      <div :class="activeValue=='神经症性问题'?'show':'hide'">
        <div class="field-item">
          <label>问题类型：</label>
          <select name="questionType2" v-model="questionType" v-on:change="showR=true">
            <option value="">请选择</option>
            <option value="1">焦虑神经症</option>
            <option value="2">抑郁神经症</option>
            <option value="3">强迫性神经症</option>
            <option value="4">疑病神经症</option>
            <option value="5">恐惧神经症</option>
            <option value="6">神经衰弱</option>
          </select>
        </div>
      </div>
      <div :class="activeValue=='严重心理困扰评估'?'show':'hide'">
        <mt-field label="严重心理困扰：" placeholder="依据DSM-Ⅳ、ICD-10或CCMD-3诊断标准" v-model="serious"></mt-field>
        <div class="chk-box risk-box bt0">
          <label class="risk">
            <input type="checkbox" value="危机风险评估" name="risk" v-model="risk">
            危机风险评估
          </label>
        </div>
      </div>
      <div :class="activeValue=='危机个案'?'show':'hide'">
        <div class="chk-box risk-box chk-box2">
          <label class="risk" v-for="(item,index) in crisisList">
            <input type="checkbox" :value="item" name="crisis" v-model="crisis[index]">
            {{item}}
          </label>
          <label>
            <input type="checkbox" :value="other" name="crisis1">
            {{"6.其他"}}
            <input type="text" class="otherInput" v-model="other" value="">
          </label>
        </div>
      </div>
      <div :class="activeValue=='管理者推荐'?'show':'hide'">
        <div class="chk-box risk-box chk-box2">
          <label>
            <input type="radio" :value="commend[0]" name="commend2">
            {{"1.管理者"}}
            <input type="text" class="otherInput" v-model="commend[0]" value="">
          </label>
          <label>
            <input type="radio" :value="commend[1]" name="commend2">
            {{"2.员&#12288;工"}}
            <input type="text" class="otherInput" v-model="commend[1]" value="">
          </label>
        </div>
        <div class="chk-box risk-box">
          <label class="risk">
            <input type="checkbox" value="危机风险评估" name="risk" v-model="risk">
            危机风险评估
          </label>
        </div>
      </div>
      <div :class="activeValue=='主动干预'?'show':'hide'">
        <div class="chk-box chk-box2">
          <label v-for="(item,index) in interveneList">
            <input type="radio" :value="item" name="intervene" v-model="intervene">
            {{item}}
          </label>
        </div>
      </div>

      <div v-show="showR" class="chk-box risk-box">
        <label class="risk">
          <input type="checkbox" value="危机风险评估" name="risk" v-model="risk">
          危机风险评估
        </label>
      </div>
      <div class="btnBox">
        <button class="closeBtn" @click="nextGo">下一步</button>
      </div>

    </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
    export default {
      name: "record",
      data(){
        return{
          showTitle:'个案记录',
          time:'',
          withWork:"",
          questionList:["一般心理困扰","神经症性问题","严重心理困扰评估","危机个案","管理者推荐","主动干预"],
          queList:[
            [],
            ["1.恋爱关系","2.夫妻关系","3.与原生家庭关系","4.婆媳关系","5.其他家庭成员关系","6.失恋","7.离异","8.婚外恋","9.家庭暴力"],
            ["1.不良行为矫正","2.发展适应","3.亲子沟通","4.性心理","5.教育方法","6.考试焦虑","7.学习困难","8.升学压力","9.厌学"],
            ["1.朋友关系","2.上下级关系","3.亲子沟通","4.同事关系","5.师生关系","6.同学关系"],
            ["1.工作适应","2.绩效压力","3.工作收入","4.工作疑难问题","5.工作与家庭平衡","6.工作影响生理健康"],
            ["1.职业兴趣","2.职业选择","3.职业规划","4.职业倦怠","5.中年危机"],
            ["1.性取向","2.性偏好"],
            ["1.人格发展","2.认知调整","3.情绪管理","4.自我价值"],
            ["1.不孕不育","2.二胎问题","3.产前抑郁","4.产后抑郁"]
          ],
          crisis:[],
          crisisList:["1.自杀","2.伤害他人","3.丧亲","4.家庭暴力","5.严重职场暴力"],
          activeValue:"",
          questionType:'',
          queValue:"",
          risk:"",
          other:"",
          showRisk:false,
          commend:[],
          interveneList:["A级 危机个案，需密切关注","B级 存在潜在危险，需持续关注","C级 状态良好，无需继续关注"],
          intervene:"",
          serious:""
        }
      },
      created(){
      },
      watch:{
        activeValue(val){
          this.questionType="";
        },
        questionType(val){
          this.queValue="";
        },
        // intervene(val){
        //   console.log(val)
        // }
      },
      computed:{
        showR:{
          get:function(){
            console.log(this.showRisk);
            return this.showRisk
          },
          set:function(val){
            this.showRisk=val;
          }
        }
      },
      components:{
        appHeader
      },
      methods:{
        backUp(){
          this.$refs.child.backUp();
        },
        getOther(obj){
          let arr=obj[obj.length-1];
          return arr;
        },
        nextGo(){
          if(this.withWork){
            this.$router.push("/record2")
          }else {
            this.$router.push("/record3")
          }
        }
      }
    }
</script>

<style scoped lang="less">
  .record{
    padding-bottom: 150px;
    overflow: auto;
  }
  .show{
    display: block;
    position: relative;
  }
  .hide{
    display: none;
  }
  .tip-box{
    border-bottom: 1px solid #ededed;
  }
  .timeAfter{
    position: relative;
    &:after{
      content: "分钟";
      display: block;
      position: absolute;
      right: 36px;
      border-bottom: 0;
      font-size: 30px;
      width: 80px;
      height: 84px;
      line-height: 84px;
    }
  }

  .chk-box{
    padding-left: 50px;
    padding-right: 50px;
    border-bottom: 1px solid #ededed;
    &.chk-box2{
      padding: 20px 50px;
      border: 0;
      overflow: auto;
      label{
        width: 100%;
        float: left;
        &:nth-child(2n){
          width:100%;
        }
      }
    }
    label{
      position: relative;
      display: inline-block;
      font-size: 30px;
      width: 55%;
      line-height: 80px;
      &:nth-child(2n){
        width: 45%;
      }
    }
    input[type='radio'],input[type='checkbox'] {
      width: 38px;
      height: 38px;
      overflow: visible;
      background: url("../../assets/img/consult/radioBg.png") no-repeat;
      background-size: 100%;
      vertical-align: sub;
      border: 0;
      outline: 0;
      -webkit-appearance: none;
    }

    input:checked{
      position: relative;
      background: #fff;
    }

    input:checked::after {
      position: absolute;
      background: url("../../assets/img/consult/radioicon.png") no-repeat;
      background-size: 100%;
      content: "";
      width: 38px;
      height: 38px;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
  }
  .risk-box{
    width: 100%;
    border-top: 1px solid #ededed;
    &.bt0{
      border-top: 0;
    }
  }
  .closeBtn{
  }
  .btnBox{
    width: 100%;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    padding-bottom: 28px;
  }
  .otherInput{
    position: absolute;
    width: 240px;
    height: 46px;
    top: 14px;
    outline: 0;
    border: 0;
    border-bottom: 2px solid #545454;
    font-size: 30px;
    padding-left: 10px;
  }
  .heightAuto{
    height: auto;
  }
</style>



