<template>
  <div id="organization">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box">机构信息</h3>
    <div class="e-field">
      <mt-field label="机构名称" placeholder="请输入名称" v-model="username"></mt-field>
      <div class="field-item upState">
        <div class="field-title">营业执照</div>
        <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
                @upload="getImgUrl" class="upload"></upload>
      </div>
      <div class="field-item">
        <label for="type">机构类别</label>
        <select name="" id="type">
          <option>请选择</option>
          <option>心理</option>
          <option>法律</option>
          <option>财务</option>
          <option>医疗</option>
        </select>
      </div>
      <div class="field-item">
        <label for="city">机构所在地</label>
        <button @click="showArea" id="city" class="show-btn">{{btnTxt}}</button>
        <div class="pop-box" v-show="popupVisible">
          <button @click="hideArea" class="close-btn">确认</button>
          <eap-area v-on:childEvent="changeCity"></eap-area>
        </div>
      </div>
      <mt-field label="负责人姓名" placeholder="请输入真实姓名" v-model="linkman"></mt-field>
      <mt-field label="联系电话" placeholder="请输入联系电话" type="tel" v-model="linkphone"></mt-field>
      <mt-field label="网址(选填)" placeholder="请输入网址" v-model="linkman"></mt-field>
    </div>
    <div class="strong-line2"></div>
    <div class="e-field">
      <h3 class="area-title">机构简介</h3>
      <div class="area-box">
        <textarea class="area-input" placeholder="2000字以内"></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title">邮寄地址<b>（选填）</b></h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="参考格式：省-市-区/县-具体门牌号"></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title">备注<b>（选填）</b></h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="200字以内"></textarea>
      </div>
    </div>
    <div class="btn-box">
      <router-link to="/organization/next"><div class="submit-btn">保存并下一步</div></router-link>
    </div>
  </div>
</template>


<script>
  import appHeader from "@/components/public/appHeader"
  import upload from "@/components/public/upload";
  import eapArea from "@/components/public/area";

  export default {
    name: "organization",
    data(){
      return{
        showTitle:"咨询机构入驻",
        year:[],
        username:"",
        phone:"",
        linkman:"",
        linkphone:"",
        birthday:'',
        selectData: {
          defaultIndex: 0, //默认选中的是第几个
          selectStatus: false, // 通过selectStatus来控制下拉框的显示/隐藏
        },
        popupVisible:false,
        btnTxt:"请选择",
        imgUrl:""
      }
    },
    components:{
      appHeader,
      eapArea,
      // oSelect,
      //注册组件
      upload
    },
    created:function(){
    },
    methods:{
      backUp:function () {
        this.$refs.child.backUp();
      },
      showArea(){
        this.popupVisible=true;
      },
      hideArea(){
        this.popupVisible=false;
      },
      changeCity(data){
        console.log(data);
        this.btnTxt=data;
      },
      //接收子组件emit的事件
      getImgUrl(data) {
        //data  得到的就是返回的图片路径的相关参数
        console.log(data)
      }
    }
  }
</script>

<style scoped lang="less">
</style>





