<template>
  <div id="record4" class="record">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box">咨询过程</h3>
    <div class="e-field">
      <h3 class="area-title2">咨询师评估（每次均需了解是否有危机？）</h3>
      <div class="area-box height300">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title2">咨询目标</h3>
      <div class="area-box height300">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="area-title2">干预过程及方法（支持、认知重建、问题解决技巧、行为改变计划、家庭作业等等）</h3>
      <div class="area-box height300">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="btnBox">
      <button class="closeBtn" @click="goTo('/record5')">下一步</button>
    </div>

  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "record3",
    data(){
      return{
        showTitle:'个案记录',
      }
    },
    created(){
    },
    watch:{
    },
    computed:{
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$refs.child.backUp();
      }
    }
  }
</script>

<style scoped lang="less">
  .record{
    padding-bottom: 150px;
    overflow: auto;
  }
  .tip-box{
    border-bottom: 1px solid #ededed;
  }

  .btnBox{
    width: 100%;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    padding-bottom: 28px;
  }

</style>




