<template>
  <div id="organizationNext">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box">填写入驻信息</h3>
    <div class="e-field">
      <mt-field label="预约电话1" placeholder="请输入联系电话" v-model="username" type="number"></mt-field>
      <mt-field label="预约电话2" placeholder="请输入联系电话" v-model="username" type="number"></mt-field>
      <mt-field label="咨询室数量" placeholder="请输入正确数量" v-model="linkman"></mt-field>
      <div class="tips">温馨提示：用于系统判定同一时段允许为本机构咨询师安排的最多面询个数。</div>
    </div>
    <div class="e-field">
      <h3 class="area-title">面对面咨询地址</h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="参考格式：省-市-区/县-具体门牌号"></textarea>
      </div>
    </div>
    <div class="btn-box">
      <router-link to="/preview/organ"><div class="submit-btn">预览</div></router-link>
      <router-link to="/"><div class="submit-btn">保存并提交</div></router-link>
    </div>
  </div>
</template>


<script>
  import appHeader from "@/components/public/appHeader"

  export default {
    name: "next",
    data(){
      return{
        showTitle:"咨询机构入驻",
        username:"",
        phone:"",
        linkman:"",
        linkphone:"",
        birthday:'',
      }
    },
    components:{
      appHeader,
    },
    created:function(){
    },
    methods:{
      backUp:function () {
        this.$refs.child.backUp();
      },
    }
  }
</script>

<style scoped lang="less">
.tips{
  width:750px;
  font-size:24px;
  font-weight:400;
  color:rgba(177,177,177,1);
  line-height:36px;
  padding-top: 30px;
  padding-left: 36px;
  padding-right: 36px;
  padding-bottom: 0;
}

</style>






