import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store=new Vuex.Store(
  {
      state:{
        stepClass:[1],
        stepList:[{index:1,txt:'个人信息'},{index:2,txt:'资质能力'},{index:3,txt:'咨询设置'},{index:4,txt:'提交申请'}]
      },
      mutations:{
        stepGo(state,i){
          let len=state.stepClass.length;
          if(i>len){
            while (len<i){
              state.stepClass.push(len+1);
              len++
            }
          }else {
            while (len>i){
              state.stepClass.pop();
              len--
            }
          }
        }
      }

  });
export default store;



