<template>
  <div id="record2" class="record">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box">与工作相关</h3>
    <div style="min-height: 160px">
      <div class="field-item">
        <label>工作因素：</label>
        <select name="questionType1" v-model="questionType">
          <option value="">请选择</option>
          <option value="1">工作中的压力问题</option>
          <option value="2">工作中的情绪问题</option>
          <option value="3">工作中的人际问题</option>
          <option value="4">工作中的适应问题</option>
          <option value="5">工作中的环境问题</option>
          <option value="6">工作中的职业发展</option>
          <option value="7">公司的管理制度问题</option>
        </select>
      </div>
      <div class="chk-box chk-box2">
        <div :class="questionType=='1'?'show':'hide'">
          <label v-for="(item,index) in queList[1]" :key="index">
            <input type="checkbox" :value="item" name="que1" @click="getVal(item)">
            {{item}}
          </label>
        </div>
        <div :class="questionType=='2'?'show':'hide'">
          <label v-for="(item,index) in queList[2]" :key="index">
            <input type="checkbox" :value="item" name="que1" @click="getVal(item)">
            {{item}}
          </label>
        </div>
        <div :class="questionType=='3'?'show':'hide'">
          <label v-for="(item,index) in queList[3]" :key="index">
            <input type="checkbox" :value="item" name="que1" @click="getVal(item)">
            {{item}}
          </label>
        </div>
        <div :class="questionType=='4'?'show':'hide'">
          <label v-for="(item,index) in queList[4]" :key="index">
            <input type="checkbox" :value="item" name="que1" @click="getVal(item)">
            {{item}}
          </label>
        </div>
        <div :class="questionType=='5'?'show':'hide'">
          <label v-for="(item,index) in queList[5]" :key="index">
            <input type="checkbox" :value="item" name="que1" @click="getVal(item)">
            {{item}}
          </label>
        </div>
        <div :class="questionType=='6'?'show':'hide'">
          <label v-for="(item,index) in queList[6]" :key="index" @click="getVal(item)">
            <input type="checkbox" :value="item" name="que1" >
            {{item}}
          </label>
        </div>
        <div :class="questionType=='7'?'show':'hide'">
          <label v-for="(item,index) in queList[7]" :key="index" @click="getVal(item)">
            <input type="checkbox" :value="item" name="que1" >
            {{item}}
          </label>
        </div>
      </div>
    </div>
    <div class="strong-line"></div>
    <div class="e-field">
      <h3 class="area-title2">请具体描述该项工作因素如何对咨询问题造成影响? (如有反党反社会倾向，请及时反馈）</h3>
      <div class="area-box height300">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="btnBox">
      <button class="closeBtn" @click="goTo('/record3')">下一步</button>
    </div>

  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "record2",
    data(){
      return{
        showTitle:'个案记录',
        queList:[
          [],
          ["1.工作负荷大","2.考核指标重","3.加班多","4.出差时间长","5.工作危险性大","6.工作责任重大","7.工作内容单调、重复性工作多","8.工作中有性别歧视","9.工作不被认可或尊重","10.部门内部竞争大","11.平时考试多"],
          ["1.对单位满意度","2.对领导满意度","3.对工作本身满意度","4.对待遇满意度"],
          ["1.和直接领导之间的关系","2.和其他部门的同事关系","3.和本部门的同事关系","4.和外部客户的关系"],
          ["1.新员工适应工作","2.更换领导适应工作","3.变化部门或岗位适应工作"],
          ["1.办公环境简陋","2.办公地点偏远","3.影响家庭生活","4.食堂住宿交通不便","5.工作场所噪音大","6.团队氛围不适应","7.员工活动较少","8.培训激励较少"],
          ["1.竞聘上岗","2.缺乏晋升通道","3.晋升机会小","4.晋升制度不合理","5.晋升过程中不公平、不公开、不透明","6.工作中自我能力提升"],
          ["1.管理流程过于复杂","2.部门内工作任务分配不均","3.岗位设置不合理","4.工作职责划分不清晰","5.公司不重视员工个人发展","6.公司考评制度不合理"],
        ],
        questionType:""
      }
    },
    created(){
    },
    watch:{
    },
    computed:{
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$refs.child.backUp();
      },
      getVal(d){
        console.log(d)
      }
    }
  }
</script>

<style scoped lang="less">
  .record{
    padding-bottom: 150px;
    overflow: auto;
  }
  .show{
    display: block;
    position: relative;
  }
  .hide{
    display: none;
  }
  .tip-box{
    border-bottom: 1px solid #ededed;
  }

  .chk-box{
    padding-left: 50px;
    padding-right: 50px;
    border-bottom: 1px solid #ededed;
    &.chk-box2{
      padding: 20px 50px;
      border: 0;
      overflow: auto;
      label{
        width: 100%;
        float: left;
        &:nth-child(2n){
          width:100%;
        }
      }
    }
    label{
      position: relative;
      display: inline-block;
      font-size: 30px;
      width: 55%;
      line-height: 80px;
      &:nth-child(2n){
        width: 45%;
      }
    }
    input[type='radio'],input[type='checkbox'] {
      width: 38px;
      height: 38px;
      overflow: visible;
      background: url("../../assets/img/consult/radioBg.png") no-repeat;
      background-size: 100%;
      vertical-align: sub;
      border: 0;
      outline: 0;
      -webkit-appearance: none;
    }

    input:checked{
      position: relative;
      background: #fff;
    }

    input:checked::after {
      position: absolute;
      background: url("../../assets/img/consult/radioicon.png") no-repeat;
      background-size: 100%;
      content: "";
      width: 38px;
      height: 38px;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
  }

  .btnBox{
    width: 100%;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    padding-bottom: 28px;
  }

</style>




