<template>
  <div id="step1" class="step-container">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <ul class="step-list">
      <li :class="item.index==activeClass[index]?'active':''" v-for="(item,index) in stepList">
        <img :src="getUrl(item.index)" alt="" v-if="activeClass[index]==item.index">
        <img :src="getUrl('2'+item.index)" alt="" v-if="activeClass[index]!=item.index">
        <span>{{item.txt}}</span>
      </li>
    </ul>
    <div class="field-item upState">
      <div class="field-title">上传头像</div>
      <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
              @upload="getImgUrl" class="upload"></upload>
      <p class="tips">头像是来访者获取对咨询师的直观印象的重要途径，建议您精心挑选。</p>
    </div>
    <div class="field-item upState id-card">
      <div class="field-title">上传身份证</div>
      <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
              @upload="getImgUrl" class="upload"></upload>
      <upload :uploadType="`head`" :imgWidth="`1.6rem`" :imgHeight="`1.6rem`" :imgUrl="imgUrl"
              @upload="getImgUrl" class="upload"></upload>
      <p class="tips">正反面两张</p>
    </div>
    <mt-field label="真实姓名" placeholder="请输入真实姓名" v-model="username"></mt-field>
    <mt-field label="身份证号" placeholder="请输入身份证号" v-model="idCard" type="number"></mt-field>

    <div class="field-item">
      <label for="sex">性别</label>
      <select name="" id="sex">
        <option>请选择</option>
        <option>男</option>
        <option>女</option>
      </select>
    </div>
    <div class="field-item">
      <label for="city">城市</label>
      <button @click="showArea" id="city" class="show-btn">{{btnTxt}}</button>
      <div class="pop-box" v-show="popupVisible">
        <button @click="hideArea" class="close-btn">确认</button>
        <eap-area v-on:childEvent="changeCity"></eap-area>
      </div>
    </div>
    <div class="field-item">
      <label for="edu">学历</label>
      <select name="" id="edu">
        <option>请选择</option>
        <option>高中及以下</option>
        <option>专科</option>
        <option>本科</option>
        <option>硕士</option>
        <option>博士及以上</option>
      </select>
    </div>
    <div class="field-item">
      <label for="marry">婚姻状况</label>
      <select name="" id="marry">
        <option>请选择</option>
        <option>未婚</option>
        <option>已婚</option>
        <option>离异</option>
      </select>
    </div>
    <div class="field-item">
      <label for="children">有无子女</label>
      <select name="" id="children">
        <option>请选择</option>
        <option>无</option>
        <option>1人</option>
        <option>2人</option>
        <option>3人</option>
        <option>4人</option>
        <option>5人以上</option>
      </select>
    </div>
    <mt-field label="个人电话" placeholder="用于接收咨询短信" v-model="phone" type="number"></mt-field>
    <mt-field label="从业年限" placeholder="请输入年限" v-model="year" type="number"></mt-field>
    <div class="field-item">
      <label for="type">咨询类型</label>
      <select name="" id="type">
        <option>请选择</option>
        <option>心理</option>
        <option>法律</option>
        <option>财务</option>
        <option>医疗</option>
      </select>
    </div>
    <div class="field-item">
      <label for="theme">擅长主题</label>
      <select name="" id="theme">
        <option>请选择</option>
        <option>心理</option>
        <option>法律</option>
        <option>财务</option>
        <option>医疗</option>
      </select>
    </div>
    <div class="field-item">
      <label for="crowd">擅长人群</label>
      <select name="" id="crowd">
        <option>请选择</option>
        <option>心理</option>
        <option>法律</option>
        <option>财务</option>
        <option>医疗</option>
      </select>
    </div>
    <mt-field label="咨询语言" placeholder="请输入您的擅长语言(例：普通话，粤语)" v-model="language"></mt-field>
    <div class="e-field">
      <h3 class="area-title">联系地址</h3>
      <div class="area-box height100">
        <textarea class="area-input" placeholder="参考格式：省-市-区/县-具体门牌号"></textarea>
      </div>
    </div>


    <div class="btn-box">
      <router-link to="/eap/step2"><div class="submit-btn" @click="goStep(2)">保存并下一步</div></router-link>
    </div>
  </div>
</template>

<script>
  import eapArea from "@/components/public/area";
  import upload from "@/components/public/upload";
  import appHeader from '@/components/public/appHeader';
    export default {
      name: "step1",
      data(){
        return{
          imgUrl:"",
          activeClass:[],
          imgBaseUrl:"../../assets/img/seminar/",
          stepList:this.$store.state.stepList,
          showTitle:'咨询师入驻',
          username:'',
          language:"",
          idCard:'',
          selectData: {
            defaultIndex: 0, //默认选中的是第几个
            selectStatus: false, // 通过selectStatus来控制下拉框的显示/隐藏
          },
          popupVisible:false,
          btnTxt:"请选择",
          phone:'',
          year:''
        }
      },
      created(){
        this.$store.commit("stepGo",1);
      },
      mounted(){
        this.activeClass=this.$store.state.stepClass;
      },
      components:{
        upload,
        appHeader,
        eapArea
      },
      methods:{
        //接收子组件emit的事件
        getImgUrl(data) {
          //data  得到的就是返回的图片路径的相关参数
          console.log(data)
        },
        //获取图片地址
        getUrl(icon){
          return require("@/assets/img/seminar/"+icon+".png");
        },
        backUp(){
          this.$refs.child.backUp();
        },
        showArea(){
          this.popupVisible=true;
        },
        hideArea(){
          this.popupVisible=false;
        },
        changeCity(data){
          console.log(data);
          this.btnTxt=data;
        },
      }
    }
</script>

<style lang="less">
  #step1 {
    .id-card .avatar{
      &:last-of-type{
        margin-left: 0;
      }
    }
  }

  .id-card .tips{
    width: 172px;
  }
</style>



